import React, { useState, useEffect } from 'react';

function GetScore(props){
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [score, setScore] = useState(0);
  useEffect(() => {
    const passage = props.passage.replace(/[!'’()*]/g, "");
    const input = props.userInput.replace(/[!'’()*]/g, "");
    const url = "https://api.allorigins.win/get?url=https://shiny.kwyk.in/mobile/getscore/"+encodeURIComponent(passage)+"/"+encodeURIComponent(input)
    fetch(url)
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          let score = JSON.parse(result.contents);
          setScore(score);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])
  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div>Loading...</div>;
  } else {
    return (
      <div className="white">
        {score}
      </div>
    );
  }
}

export default GetScore;
