import React, { useState }  from "react";
import { Router, Switch, Route } from "react-router-dom";
import ReadText from "./readText";
import history from './components/history';
import TestYourMemory from "./testYourMemory"
import DisplayScore from "./displayScore"
import Home from "./home"
import GetContent from "./components/getContent";



function Routes(){
    const [passage, setPassage] = useState("");
    const [userInput,setUserInput] = useState("");
    const [minutes, setMinute] = useState(0);
    const [seconds, setSecond] = useState(0);
    const [index, setIndex] = useState(0);

    function getPassage(usedPassage){
      setPassage(usedPassage);
    }

    function getUserInput(userInput){
      setUserInput(userInput);
    }

    function getTime(minutes, seconds){
      setMinute(minutes);
      setSecond(seconds);
    }

    function getIndex(indexNo){
      setIndex(indexNo);
    }

    return(
      <Router history={history} >
      <Switch>
        <Route path="/" exact render={() => <Home getTime={getTime}/>} />
        <Route path="/ReadText"  render={() => <ReadText  getPassage={getPassage} getTime={getTime}
        getIndex={getIndex} index={index}/>} />
        <Route path="/TestYourMemory" render={() => <TestYourMemory getUserInput={getUserInput}/>} />
        <Route path="/Score" render={() => <DisplayScore passage={passage} userInput={userInput}
        minutes={minutes} seconds={seconds}/>} />
      </Switch>
      </Router>
    );

}

export default Routes;
