import React, { useState, useEffect } from "react";

function GetContent(props){
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [items, setItems] = useState([]);
  const [index, setIndex] = useState(props.index);
  useEffect(() => {
    fetch("https://api.allorigins.win/get?url=https://shiny.kwyk.in/mobile/get/communication_skills/comprehension/words")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          if(index >= result.length-1){
            props.getIndex(0);
          }
          else{
            props.getIndex(index+1);
          }
          let passage = JSON.parse(result.contents)
          setItems(passage[index]);

        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])
  if (error) {
    return "Error: {error.message}";
  } else if (!isLoaded) {
    return "Loading...";
  } else {
    props.getPassage(items)
    return (items);
  }
}


export default GetContent;
