import React from 'react';
import './components/static/css/header.css';
import knowlogo from './components/static/images/kwyk_logo.png';
import history from './components/history';

function Home1(){
  return(
    <div className="container">
      <div className="row">
        <div className="col-sm-2"></div>
        <div className="col-sm-8">
          <Header />
          <Title />
          <Introduction />
          <Form />
        </div>
        <div className="col-sm-2"></div>
      </div>
    </div>
  );
}

export default Home1;

function Header(props){
  return(
        <div className="logo">
            <img src={knowlogo} className="logoimg" alt="logo" />
        </div>
  );
}

function Title(){
  return(
        <div className="white">
        <br />
        <br />
            <span className="title">Reading and Comprehension</span>
        </div>
  );
}

function Introduction(){
  return(
    <div className="intro">
            <br />
            <br />
            Here is an exericise to test your reading and comprehension skills.
            <br/>
            <ul>
              <li>You will be given a passage of about 50 words. </li>
              <li>Read the passage</li>
              <li>After you finish reading, enter the word/phrases that you can recall from the passage</li>
              <li>You will be given a recall score and speed</li>
              <li>You can choose to read another passage or exit the test</li>
            </ul>
        </div>
  );
}

function Form(){
  function handleClick(e){
    history.push('/ReadText');
  }
  return(
    <form>
      <div>
        <input type="submit" className="start-button fixed-bottom"
          value="START" onClick={handleClick}/>
      </div>
    </form>
  );
}
