import React from 'react';
import Header from "./components/kwykHeader";
import './components/static/css/header.css';
import GetScore from "./components/getScore";
import history from './components/history';

function DisplayScore(props){
  let score = <GetScore passage={props.passage} userInput={props.userInput}/>;
  function handleClick(e){
    if(e.target.value === "Exit"){
      history.push('/');
    }
    else{
      history.push('/ReadText');
    }
  }
  return(
    <div className="container">
      <div className="row">
        <div className="col-sm-2"></div>
          <div className="col-sm-8 white">
            <Header />
            <div className="row info-text" style={{"fontSize": "30px"}}>
              Your Score
            </div>
            <br />
            <br />
            <form >
            <div className="white">
              <div className="row white">
                  <div className="col-sm-1 white"></div>
                  <div className="col-sm-9 score">
                  <DisplayRecallScore score={score}/>
                  <DisplayTime minutes={props.minutes} seconds={props.seconds}/>
                  </div>
                  <div className="col-sm-2 white"></div>
                </div>
                <div>
                  <input type="submit" className="read-button fixed-bottom" value="Next Challenge" onClick={handleClick}/>
                </div>
                <br />
                <div>
                  <input type="submit" className="read-button fixed-bottom" value="Exit" onClick={handleClick}
                  />
                </div>
                </div>
            </form>
          </div>
        <div className="col-sm-2"></div>
      </div>
    </div>
  );
}

export default DisplayScore;

function DisplayRecallScore(props){
  return(
    <div className="row">
      <div className="col-sm-6 white">
          <label className="score-label">
              Recall Score:
          </label>
      </div>
      <div className="col-sm-6 white">
          <label className="score-value">
            {props.score}
          </label>
      </div>
    </div>
  );
}

function DisplayTime(props){
  return(
    <div className="row">
      <div className="col-sm-6 white">
          <label className="score-label">
              Time Taken:
          </label>
      </div>
      <div className="col-sm-6 white">
          <label className="score-value">
            {props.minutes}:{props.seconds}
          </label>
      </div>
    </div>
  );
}
