import React, {useState} from 'react';
import Header from "./components/kwykHeader";
import TimerHeader from "./components/timerHeader";
import DisplayContent from "./components/displayContent";
import './components/static/css/header.css';
import GetContent from "./components/getContent";
import Timer from "./components/timer";

function ReadText(props) {

  const [started, setStart] = useState(false);
  let time;
  function getStartStatus(started){
    setStart(started);
  }
  if (started){
    time = <Timer returnTime={props.getTime} />
  }
  else{
    time = "00:00"
  }
  const content = <GetContent getPassage={props.getPassage} getIndex={props.getIndex} index={props.index}/>

  return(
    <div className="container">
      <div className="row">
        <div className="col-sm-2"></div>
          <div className="col-sm-8 white">
            <Header />
            <TimerHeader time= {time}/>
            <DisplayContent content={content} getStartStatus={getStartStatus}/>
          </div>
        <div className="col-sm-2"></div>
      </div>
    </div>
  );
}

export default ReadText;
