import React, { useState } from 'react';
import './static/css/content.css';
import './static/css/header.css';
import history from './history';

function DisplayContent(props){
  const [textcolor, setColor] = useState("#edeff2");
  const [isStop, setStop] = useState(false);
  const [buttonText, setButtonText]= useState("Press this Button to Start Reading");
  function handleClick(e){
    if(isStop){
      history.push('/TestYourMemory');
    }
    else{
      e.preventDefault();
      setColor("#000000");
      setButtonText("STOP");
      setStop(true);
      props.getStartStatus(true);
    }
  }

  return(
    <form>
      <div className="white">
        <label className="content-label" style={{color: textcolor}} disabled>
            {props.content}
          </label>
        </div>
        <div>
          <input type="submit" className="read-button fixed-bottom"
          value={buttonText} onClick={handleClick}/>
        </div>
    </form>

  );

}

export default DisplayContent;
